.App {
  text-align: center;
  width: 100vw;
  height: 100vh;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  font-size: 40px;
  height: 100px;
  padding: 20px;
}

.circle-wrapper{
  min-width: 100%;
  min-height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.circle-body {
  position: relative;
}

.habbit-header {
  background-color: aquamarine;
  position: absolute;
  -webkit-transform-origin: 0 0;
      -ms-transform-origin: 0 0;
          transform-origin: 0 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.step {
  position: absolute;
  -webkit-transform-origin: 0 0;
      -ms-transform-origin: 0 0;
          transform-origin: 0 0;
  cursor: pointer;
}

.rightBorder {
  height: 100%;
  position: absolute;
  -webkit-transform-origin: 100% 100%;
      -ms-transform-origin: 100% 100%;
          transform-origin: 100% 100%;
  border-bottom: solid 1px black;
  border-top:  solid 1px black;
  border-right:  solid 1px black;
}

.step-text {
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  text-align: left;
  position: absolute;
  z-index: 5;
  font-size: small;
}

.leftBorder {
  height: 100%;
  position: absolute;
  border-bottom: solid 1px black;
  border-top:  solid 1px black;
  border-left:  solid 1px black;
  -webkit-transform-origin: 0 100%;
      -ms-transform-origin: 0 100%;
          transform-origin: 0 100%;
}

.corner{
  position: absolute;
  width: 2px;
  height: 2px;
  background-color: crimson;
}

.center {
  position: absolute;
  width: 2px;
  height: 2px;
  background: url("../images/vitruvian.png") center;
  background-size:contain;
}


